
.housing-list {
    
background-color: #F6F6F6;
max-width: 100%;
padding: 30px;
margin-top: 30px;
border-radius: 25px;}
    .cards {
        display: flex;
        flex-wrap: wrap;
        gap: 70px;
        justify-content: center;
       
    }
        .card {
           
            border-radius: 10px;
            position: relative;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
            overflow: hidden;
            width: 340px;
            height: 340px;
            }
            h3{
                color: white;
                width: 50%;
                position: absolute;
                bottom: 10px;
                left: 5px;
            }

            @media (max-width: 590px){
                .housing-list{
                    background-color: rgba(0, 0, 0, 0);
                    
                    padding: 0px;
                }
                .cards{
                    gap: 10px;
                }
                .card{
                    width: 100%;
                    height: 100%;

                }
                .card img{
                    object-fit: cover;
                    width: 100%;
                    height: 100%;
                }
            }       