.header{
    max-width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
   padding: 30px 0px 30px 30px;

}
.header img{
    width: 210px;
    height: 68px;
}
.nav-links{
    margin: 1%;
    font-family: sans-serif ;
    font-size: 24px;
    
}
.nav-links a {
    color: black;
    text-decoration: none;
    position: relative; 
    padding: 0px 15px 10px 15px; 
  }
  
  .nav-links a::after {
    content: '';
    display: block;
    width: 0;
    height: 2px;
    background: black;
    transition: width 0.3s;
    position: absolute;
    bottom: 0;
    left: 10px;
  }
  
  .nav-links a:hover::after,
  .nav-links a.active::after {
    width: 80%;

  }
  @media (max-width: 590px) {
    .header{
      padding: 30px 15px 30px 30px;
    }
    .header img{
      width: 145px;
      height: 46.89px;
  }
.nav-links{
  font-size: 12px;
  font-weight: 500;
}
  }