.not-found{
    display: flex;
    flex-direction: column;
    
    align-items: center;
    padding: 50px 50px 50px 50px;
    text-align: center;
}
.h1notfount{
    color: #FF6060;
    font-size: 288px;
    font-weight: 700;
    margin-top: 0px;
}
.parnotfound{
    color: #FF6060;
    font-size: 36px;
    font-weight: 500;
}
.return{
    color: black;
}
@media (max-width: 590px){
    .h1notfount{
        font-size: 96px;
    }
    .parnotfound{
        font-size: 18px;
        
    }
}